import React, { Component } from "react";
import { BrowserRouter as Router , Route, Link, withRouter } from "react-router-dom";
import $ from "jquery";

class HeaderAdmin extends Component {

    componentDidMount()
    {
         $(".all-close").click(function(){
          var i=$(this).parent().attr("class")
           var j=$(this).parent().removeClass("active")
            $("#drop-down").css("display", "none");
            console.log(i)
       });
   
   
    
   
       $("#drop-btn").click(function(){

        $('#drop-down').toggle("slow",function(){});
            if(window.innerWidth>768)
            {
              console.log(window.innerWidth)
              $('#mymain1').toggleClass("active");
            } 
       });
      
      $("#main-close").click(function(){
           $("#drop-down").css("display", "none");
         
         
       });
   
   
       $(".sub-close-class").click(function(){
          var i=$(this).parent().attr("class")
           var j=$(this).parent().removeClass("active")
            console.log(i)
       });
   
     $("#main-btn li button").click(function(){
             
       var t= $(this).data("target"); 
      var i=$(t).attr("id")
      i="#"+i;
      $(".active").removeClass('active')
      $(i).addClass("active");
      
      });
    }
    render() {
        return (
            <header style={{borderBottom:'1.5px solid #DA6A3C '}}>
 
 <nav className="navbar navbar-inverse">
      <div className="container-fluid">
       
        <div className="navbar-header">
          <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
            <span className="icon-bar"></span>
            <span className="icon-bar"></span>
            <span className="icon-bar"></span> 
          </button>
          <Link className="navbar-brand" to="/">पुष्करणा <span>वंशावली</span></Link>
        </div>
        
        <div className="collapse navbar-collapse" id="myNavbar">
          
          <ul className="nav navbar-nav">                                        
           <li><button className="btn btn-link" id="drop-btn">वंशावली</button></li>
           <li><Link   to="/about" >परिचय</Link></li>
            <li ><Link to="/history">इतिहास</Link></li>
            <li><Link to="/culture">कुलदेवी</Link></li>
            <li><Link  to="/add" >जोड़ना</Link></li> 
            <li><Link to="/ancestors">खोजें </Link></li>

            <li><Link to="/contact">संपर्क कीजिये</Link></li>  
             // <li><Link   to="/register" >साइन अप करें</Link></li>
         // <li><Link  to="/admin" >व्यवस्थापक</Link></li>
          </ul>
          
        </div>
      </div>
</nav>
</header>
            
        );
    }
}
export default HeaderAdmin;




