import React, { Component } from "react";
import  axios from 'axios';
import './cult.css';
import api from "../api";
const host=api();
class Culture extends Component {
	constructor(props) {
        super(props);
        this.state={
			name:[],
			style:'margin-top:3em'
		}
	 
    }
    
	componentDidMount() {
		const config={
    dataType:"json",
    contentType: "application/json"
   }
		axios.get(host+'/main/culture') // fetch from Express.js server
		.then(response =>{
			const names=response.data;
			console.log(names)
			this.setState({name:names});
		})
	}
	componentWillUnmount()
	{
		this.setState({name:[]});
	}

    render() {
		
        return (
            
			<div>
                <div className="header-banner" style={{minHeight:"4em"}}>
			        <div className="container">
							<h3>कुलदेवी</h3>
			        </div>
			    </div>
				<div className="content">
					<div className="Designers-section">
						<div className="container">
							<div className="Designers-grids">
								{ 
									this.state.name.length > 0 ? this.state.name.map(function(data) {
									return (// <h1 key={data.id}>{data.gotra_name}</h1>	;
									
							< div className="Designers1 cult" key={data.id}>
								<div className="col-md-8 col-md-offset-4 Designer-grid1">
								<h2> {data.gn} </h2>
									<h4> {data.name} </h4>
								</div>
								<div  className="col-md-4 Designer-grid">
								<a href="#" className="mask">
									<img src={data.image} className=" mask img-responsive zoom-img cimg" alt=""/></a>
								</div>
								<div className="col-md-8 Designer-grid1">
								
									
									<p className="desc">{data.desc}</p>
								</div>
								<div className="clearfix"></div>
									</div>);
							 }): null
							}  
							</div>
							 
				</div>
			</div>
            </div>
			</div>
        );
    }
}
export default Culture;




