import React, { Component } from "react";
import  axios from 'axios';
import './contact.css';
import api from "../api";
const host =api();
class Contact extends Component {

    handleSubmit(e){
        e.preventDefault();
        const name = document.getElementById('name').value;
        const email = document.getElementById('email').value;
        const message = document.getElementById('message').value;
        axios({
            method: "POST", 
            url:host+"/main/send", 
            data: {
                name: name,   
                email: email,  
                message: message
            }
        }).then((response)=>{
            if (response.data.msg === 'success'){
                alert("Message Sent."); 
                this.resetForm()
            }else if(response.data.msg === 'fail'){
                alert("Message failed to send.")
            }
        })
    }
    render() {
        return (
            <div>
                <div className="header-banner">
                    <div className="container">
                        <h3>संपर्क</h3>
                    </div>
		        </div>
				<div className="content">
				    <div className="contact">
                        <div className="container">
                            <div className="google-map">
                                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d223.58021569540352!2d73.01428235855435!3d26.284909596154712!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39418c3522b74df3%3A0x52c5226e86fbb641!2zMjbCsDE3JzA1LjgiTiA3M8KwMDAnNTIuOCJF!5e0!3m2!1sen!2sin!4v1540280376475" width="600" height="450" frameborder="0"  allowfullscreen></iframe>
                            
                            </div>
                            <div className="contact-grids">
                                <div className="col-md-6 contact-left contactadd">
                                    <address className="address">
                                        <p>Mr. Sada Shivan Vyas<br/>
                                        S/O Dr. J. M. Vyas</p>
                                        <p>Behind SBI Bank</p>
                                        <p>Outside Jalori Gate</p>
                                        <p>Jodhpur, Rajasthan</p>
                                        <p>Mobile: +91 9414283349 </p>
                                        <p>Telephone: +91 291 2626439 </p>
                                        <p>E-mail: s.s.vyas16@gmail.com</p>
							        </address>
					            </div>
                                <div className="col-md-6 contact-right">
                                    <form id="contact-form" onSubmit={this.handleSubmit.bind(this)} className="contactadd">
                                        <input type="text" name="name" id="name" placeholder="आपका नाम"/>
                                        <input type="text" name="email" id="email" placeholder="आपका इ-मेल पता"/>
                                        <textarea name="message" id="message" placeholder="संदेश"></textarea>
                                        <input type="submit" value="भेजें"/>
                                    </form>
                                </div>
                                <div className="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default Contact;




