import React, { Component } from "react";
import "../Header.css";
import "./cast.css";
import Tree from 'react-d3-tree';
import  axios from 'axios';
import ReactDom from 'react-dom';
import api from '../api';
class Dabani extends React.Component {
  constructor(props) {
    super(props);
    this.state={myTreeData:[
    {
      name: '',
      children: [
        {
          name: 'null',
          children:[
            {
              name: 'null',
              parent: 'null'
            }
          ]
        },
        {
          name: 'null',
        },
      ],
    },
  ],
 
  zoomvalue:0,
 cast_value:'',
 empty:""
  
}

this.handleZoomin=this.handleZoomin.bind(this);

}

handleZoomin=(props)=>
{
    
    if(this.state.zoomvalue<=0.8)
    {
    props.scaleExtent=this.state.zoomvalue;
    props.scaleExtent+=.4;
    this.setState({zoomvalue:props.scaleExtent}) 
   }
   console.log(props.scaleExtent)
}
handleZoomout=(props)=>
{
   if(this.state.zoomvalue>0)
    {
    props.scaleExtent=this.state.zoomvalue;
    props.scaleExtent-=.4;
    this.setState({zoomvalue:props.scaleExtent}) 
  }
   console.log(props.scaleExtent)
}

handleChart=(s)=>
{
let host=api()
   console.log(host)
   const config={
    dataType:"json",
    contentType: "application/json"
   }
  let id= JSON.stringify(s)
         
      axios.post(host+'/main/chart',{id}) // fetch from Express.js server
    .then(response =>{
      
      
      var l=new Array()
      l=response.data;
      if(l.length===0)
      {
      console.log(l);
      this.setState({empty:"empty"})
      }
      else{
        this.setState({empty:""})
        
      function convert(array){
       var map = {}
       for(var i = 0; i < array.length; i++){
           var obj = array[i]
           if(!(obj.id in map)){
               map[obj.id] = obj
               map[obj.id].children = []
           }
   
           if(typeof map[obj.id].fname === 'undefined'){
               map[obj.id].id = obj.id
               map[obj.id].name = obj.name
               // map[obj.id].attr = obj.attr
               map[obj.id].parent= obj.parent
           }
  
   
           var parent = obj.parent || '-';
           if(!(parent in map)){
               map[parent] = {}
               map[parent].children = []
           }
   
           map[parent].children.push(map[obj.id])
       }
       return map['-']
   }

  
 
 var res=convert(l);
           // console.log(res) ;
           this.setState({myTreeData:[res]});
    }
    })
}
componentDidMount()
{
  this.handleChart(this.props.match.params.id)
  this.setState({cast_value:localStorage.getItem("cast_name")})  
}
  componentWillReceiveProps(nextProps) {

   console.log(this.props.match.params.id);
   if(this.props.match.params.id!==nextProps.match.params.id || this.props.match.params.id===nextProps.match.params.id)
   {
   this.handleChart(nextProps.match.params.id)
     console.log(nextProps.match.params.id+"="+this.props.match.params.id);
     this.setState({cast_value:localStorage.getItem("cast_name")})  
   }else
   {
    this.setState({empty:"empty"})
   }
      
    
	 }
   
  
    render() {
        return (
            <div>
              <div class="header-banner">
                    <div class="container">
                        <h3>{this.state.cast_value} </h3>
                    </div>
            </div>
				<div className="content">
				    <div className="contact">
                <div className="container-fluid">
                  <button  className="btn btn-default" id="zoomin" type="button" onClick={this.handleZoomin}>+</button>
                  <button   className="btn btn-default" id="zoomout" onClick={this.handleZoomout}>-</button>
                 <div id="treeWrapper" style={{width: '100%', height: '50em'}}>
        {this.state.empty.length===0?
       <Tree data={this.state.myTreeData} orientation={'vertical'} translate={{x:800 ,y:50 }} zoom={this.state.zoomvalue} zoomable={true}
        transitionDuration={0}
        />:<h4 className="text-center" style={{marginTop:3+'em'}}>उपलब्ध नहीं है</h4>
    }
      </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }

}
export default Dabani;




