import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import Header from "./Header";
import HeaderAdmin from "./HeaderAdmin";
import { BrowserRouter as Router , Route, Link , Redirect,Switch} from "react-router-dom";
import Footer from "./Footer";
import Home from "./pages/Home";
import About from "./pages/About";
import Culture from "./pages/Culture";
import History from "./pages/History";

import Dabani from "./caste/Dabani";
import Test from "./Test.jsx";
import Nameadd from "./pages/Add";
import Ancestors from "./pages/Ancestors";
import Login from "./pages/Login";
import Admin from "./admin/Welcome";
import Register from "./pages/Register";
import Chart from "./pages/Chart";
import Addname from "./admin/name/Add";
import Searchtree from "./admin/name/Searchtree";
import Editsearch from "./admin/name/Editsearch";
import Edit from "./admin/name/Edit";
import Contact from "./pages/Contact";
import PrivateRoute from "./PrivateRoute";

class App extends Component {
 
  constructor(props) {
    super(props);

    this.state = {
      isAdmin: false
    }
  }

  
  render() {
    return (
      <Router>
      <div>
        <div className="App">
          {this.state.isAdmin ?  <HeaderAdmin /> : <Header data={this.state.surnames} />}
            <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/about" component={About} />
            <Route exact path="/culture" component={Culture} />
            <Route exact path="/history" component={History} />
            {/* <Route exact path="/surnames" component={Surnames} />*/}
            <Route exact path="/ancestors" component={Ancestors} /> 
            {/* <Route exact path="/add" component={Add} /> */}
             <Route exact path="/contact" component={Contact} />

          
             <Route exact path="/Dabani/:id" component={Dabani} />
              {/* // <Route exact path="/Dabani/:id" render={props=><Dabani {...props}/>} /> */}
             
             
             <Route exact path="/chart/:id" component={Chart} />
            
             {/* <Route exact path="/Searchtree" component={Searchtree} /> */}
             
          
            {/*<Route exact path="/register" component={Register} />*/}

             <Route exact path="/namerequest" component={Nameadd} />
             <Route exact path="/login" component={Login} />
            
            <PrivateRoute path="/Searchtree" component={Searchtree} />
            <PrivateRoute exact path="/edit" component={Edit} />
            <PrivateRoute exact path="/editsearch" component={Editsearch} />
            <PrivateRoute exact path="/add" component={Addname} />
            <PrivateRoute path="/admin" component={Admin} />
            </Switch>
           
          <Footer />
          </div>
      </div> 
      </Router>     
    );
  }
}

export default App;



