import React, { Component } from "react";
import { BrowserRouter, Route, history, Redirect,  withRouter } from 'react-router-dom'
import Axios from "axios";
import '../admin.css';
import { line } from "d3";
import api from '../../api';
import test from "../../pages/textconvert";
const host=api();
var id='';
var sid='';
var lineage='';
var mainname='';
var mainid='';


class Edit extends Component {
    state = {
        redirect: false
      }
      
   constructor(props)
   {
       super(props);
       this.state={
           name:'',
           dob:'',
           err:'',
        //    childname:'',
           success:'',
           main:'',
           olddob:''
            
       }
       this.handlerSubmit=this.handlerSubmit.bind(this);
   }
   componentDidMount() {
    var tb=this;
      let data='';
      if (localStorage && localStorage.getItem('child')){
        data=localStorage.getItem('child');
        data= JSON.parse(data);
        console.log(data);
        id=data[0].fid;
        sid=data[0].sid;
        lineage=data[0].lineage;
        mainname=data[0].mainname;
        mainid=data[0].mainid;
        // childname=data[0].childname;    
     }
     tb.setState({main:mainname});
    //  var nd= new Date(maindob);
    //  nd= nd.toDateString();
    //  console.log(nd);
    //  tb.setState({olddob:maindob});
    }
    handlerChange=(event)=>{
        // this.setState({
        //     [event.target.name]:event.target.value
        // })
        this.setState({main:event.target.value});
        console.log(event.target.value);
    }
    handlerSubmit=(event)=>{
        const tb=this;
         tb.setState({success:''})
       event.preventDefault();
        Axios.post(host+"/main/edit",{
            name:this.state.main,
            id:id,
            mainid:mainid,
            sid:sid,
            lineage:lineage
        })
        .then(function(response) {
                tb.setState({success:'परिवर्तन हो गया है'})
            })
        }
    render() {
        return (
            <div>
                <div className="header-banner">
                    <div className="container">
                        <h3>नाम जोड़ें</h3>
                    </div>
		        </div>
				<div className="content">
				    <div className="contact">
                        <div className="container">
                            <div className="contact-grids">
                                <div className="col-md-12" >
                                
                                {this.state.success}
                                </div>
                                <div className="col-md-10 col-md-offset-1 contact-right">
                                    <form onSubmit={this.handlerSubmit}>
                                        <div className="col-md-6 col-md-offset-3">
                                            <input type="text" placeholder="नाम" id="name" name="name" value={this.state.main} onChange={this.handlerChange}/>
                                            
                                            {/* <input type="text" placeholder="उपनाम" id="surname" name="surname" value={this.state.surname} onChange={this.handlerChange}/> */}
                                            <h5></h5>
                                            {/* <input type="date" placeholder="जन्म की तारीख" className="date"  id="dob" name="dob" value={this.state.olddob} onChange={this.handlerChange} /> */}
                                            <input type="submit" value="परिवर्तन"/>
                                        </div>
                                    </form>
                                   
                                </div>
                               
                                <div className="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default withRouter(Edit);





