import React, { Component } from "react";
import { BrowserRouter, Route, history, Redirect,  withRouter } from 'react-router-dom'
import Axios from "axios";

import api from '../../api';
import test from "../../pages/textconvert";
const host=api();
var fatherid='';
var sid='';
var lineage='';
class Add extends Component {
    state = {
        redirect: false
      }
      
   constructor(props)
   {
       super(props);
       this.state={
           name:'',
           dob:'',
           err:'',
           success:'',        
       }
       this.handlerSubmit=this.handlerSubmit.bind(this);
   }
   componentDidMount() {
    var tb=this;
      let data='';
      if (localStorage && localStorage.getItem('child')){
        data=localStorage.getItem('child');
        data= JSON.parse(data);
        console.log(data);
        fatherid=data[0].fid;
        sid=data[0].sid,
        lineage=data[0].lineage;
     }
    }
    handlerChange=(event)=>{
        this.setState({
            [event.target.id]:event.target.value
        })
    }
    handlerSubmit=(event)=>{
        const tb=this;
        console.log(host);
       event.preventDefault();
       tb.setState({success:''})
       let name =test(this.state.name);
console.log(name)
        Axios.post(host+"/main/add",{
            name:name,
            fid:fatherid,
            dob:this.state.dob,
            sid:sid,
            lineage:lineage
        })
        .then(function(response) {
                tb.setState({success:'नाम जोड़ दिया गया है'})
                localStorage.clear();
            })
        }
    render() {
        return (
            <div>
                <div className="header-banner">
                    <div className="container">
                        <h3>जोड़ना</h3>
                    </div>
		        </div>
				<div className="content">
				    <div className="contact">
                        <div className="container">
                            <div className="contact-grids">
                                <div className="col-md-12" >
                                
                                {this.state.success}
                                </div>
                                <div className="col-md-10 col-md-offset-1 contact-right">
                                    <form onSubmit={this.handlerSubmit}>
                                        <div className="col-md-6 col-md-offset-3">
                                            <input type="text" placeholder="नाम" id="name" name="name" required value={this.state.name} onChange={this.handlerChange}/>
                                            
                                            {/* <input type="text" placeholder="उपनाम" id="surname" name="surname" value={this.state.surname} onChange={this.handlerChange}/> */}
                                            <h5></h5>
                                            <input type="date" placeholder="जन्म की तारीख"  id="dob" name="dob" value={this.state.dob} onChange={this.handlerChange} />
                                            <input type="submit" value="खोजें"/>
                                        </div>
                                    </form>
                                   
                                </div>
                               
                                <div className="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default withRouter(Add);





