import React from 'react';
import Tree from 'react-d3-tree';
import  axios from 'axios';
import api from "../api"
 const host =api();
class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state={myTreeData:[
    {
      name: '',
      children: [
        {
          name: 'null',
          children:[
            {
              name: 'null',
              parent: 'null'
            }
          ]
        },
        {
          name: 'null',
        },
      ],
    },
  ],
  zoomvalue:0
}

}

  componentDidMount() {
    var tb=this;
      let data=this.props.match.params.id;
//       if (localStorage.getItem('data')!==""){
//         data=localStorage.getItem('data');
//         data= JSON.parse(data);
console.log(data);
//       }
      axios.post(host+'/main/chart',{
            id:data
        })
        .then(function(response) {
      var l=response.data;

      function convert(array){
       var map = {}
       for(var i = 0; i < array.length; i++){
           var obj = array[i]
           if(!(obj.id in map)){
               map[obj.id] = obj
               map[obj.id].children = []
           }
   
           if(typeof map[obj.id].fname === 'undefined'){
               map[obj.id].id = obj.id
               map[obj.id].name = obj.name
               map[obj.id].attr = obj.attr
               map[obj.id].parent= obj.parent
           }
   
           var parent = obj.parent || '-';
           if(!(parent in map)){
               map[parent] = {}
               map[parent].children = []
           }
   
           map[parent].children.push(map[obj.id])
       }
       return map['-']
   }
   var res=convert(l);
            
           tb.setState({myTreeData:[res]});
    })
    
	 }

   handleZoomin=(props)=>
{
    
    if(this.state.zoomvalue<=0.8)
    {
    props.scaleExtent=this.state.zoomvalue;
    props.scaleExtent+=.4;
    this.setState({zoomvalue:props.scaleExtent}) 
   }
   console.log(props.scaleExtent)
}
handleZoomout=(props)=>
{
   if(this.state.zoomvalue>0)
    {
    props.scaleExtent=this.state.zoomvalue;
    props.scaleExtent-=.4;
    this.setState({zoomvalue:props.scaleExtent}) 
  }
   console.log(props.scaleExtent)
}

  render() {

  
    return (
   
        <div className="content">
            <div className="contact">
                <div className="container-fluid">
                  <button  className="btn btn-default" id="zoomin" type="button" onClick={this.handleZoomin}>+</button>
                  <button   className="btn btn-default" id="zoomout" onClick={this.handleZoomout}>-</button>
                 <div id="treeWrapper" style={{width: '100%', height: '50em'}}>
     
       <Tree data={this.state.myTreeData} orientation={'vertical'} translate={{x:800 ,y:50 }} zoom={this.state.zoomvalue} zoomable={true}
        transitionDuration={0}
        />
      </div>
            </div>
          </div>
            </div>
        
            
     
    );
  }
}
// module.exports = Dimensions()(TreeView);
export default MyComponent;


