import React, { Component } from "react";
import { BrowserRouter, Route, history, Redirect,  withRouter } from 'react-router-dom'
import api from "../api"

import './cult.css';
import  Axios from 'axios';
const host =api();
class Login extends Component {
    constructor(props){
        super(props);
        this.state={
            email:'',
            password:'',
            redirectToReferrer: false
        };
    }
    validateForm=()=>{
        return this.state.email.length>0 && this.state.password.length>0;
    }
    handlerChange=event=>{
        this.setState({
            [event.target.id]:event.target.value
        })

    }
    handlerSubmit=(event)=>{
        const that=this;

       event.preventDefault();
        Axios.post(host+'/loginn/login',{
            email:that.state.email,
            password:that.state.password,
        })
        .then(function(response) {
           
           
            localStorage.setItem("user",JSON.stringify(response));
           
            // that.props.history.push("/Searchtree");
            that.setState(()=>{
                that.setState(()=>({
                    redirectToReferrer:true
                }))
            })
        });
    };

    render() {
        const {redirectToReferrer} = this.state
        if(redirectToReferrer ===true){
            // return <Redirect to='/admin' />
             window.location.assign('/admin');
        }
        return (
            <div>
                <div class="header-banner">
                    <div class="container">
                        <h3>लॉग इन</h3>
                    </div>
		        </div>
				<div class="content">
				    <div class="contact">
                        <div class="container">
                            <div class="contact-grids">
                                <div class="col-md-6 col-md-offset-4 contact-right frm frmpad">
                                    <form onSubmit={this.handlerSubmit}>
                                        <div class="col-md-7">
                                            <input type="email" placeholder="email" name="email" id="email" className="form-control"  value={this.state.email} onChange={this.handlerChange} />
                                            <h5></h5>
                                            <input type="password" placeholder="password"  name="password" id="password" className="form-control"    value={this.state.password} onChange={this.handlerChange} />
                                            <h5></h5>
                                            <input type="checkbox"/>Remember Me
                                        </div>
                                        <div className="col-md-7">
                                            <button type="submit" id="log-btn" disabled={!this.validateForm}  className="btn btn-default">लॉग इन करें</button>
                                        </div>
                                    </form>
                                </div>
                                <div class="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default Login;




