import React, { Component } from "react";
import { BrowserRouter, Route, history,Link, Redirect,  withRouter } from 'react-router-dom'
import Axios from "axios";
import '../admin.css';
import api from '../../api';
import test from "../../pages/textconvert";
const host=api();
var sid='';
var line='';
class Searchtree extends Component {
    state = {
        redirect: false
      }
      
   constructor(props)
   {
       super(props);
       this.state={
           fname:'',
           gfname:'',
           surname:'',
           lineage:'',
           err:'',
           snames:'',
          lnames:'',
          name:'',
          child:''
       }
       this.handlerSubmit=this.handlerSubmit.bind(this);
       this.handlerInsert=this.handlerInsert.bind(this);
       this.handlerChange=this.handlerChange.bind(this);
       this.getlineage=this.getlineage.bind(this);
   }
   setlineage=event=>{
    this.setState({
        lineage:event.target.value
    });
    console.log(this.state.lineage);
    }

    componentDidMount(){
        const that=this;
        Axios.get(host+"/main/sname")
        .then(function(response){
            const surnames=response.data;
            console.log(surnames);
            that.setState({snames:surnames});
        })
    }
    getlineage=event=>{
        const that=this;
        sid=event.target.value;

        Axios.post(host+"/main/lineagename",{sid:sid})
        .then(function(response){
            const lineagenames=response.data;
            console.log(lineagenames);
            that.setState({lnames:lineagenames});
        })
    }
    handlerChange=event=>{
        this.setState({
            [event.target.id]:event.target.value
        })

    }
  
    handlerSubmit=(event)=>{


        const tb=this;
        console.log(host);
let fn=test(this.state.fname);
let gfn=test(this.state.gfname);
 tb.setState({err:''})
       event.preventDefault();
        Axios.post(host+"/main/searchtree",{
            surname:sid,
            lineage:this.state.lineage,
            fname:fn,
            gfname:gfn,
        })
        .then(function(response) {
            if(response.status=='200' && response.data.er==1){
               tb.setState({err:'नाम नही मिला है'})
            }
            else{
                const names=response.data;
                console.log(names)
            line=names[0].line;
			tb.setState({name:names});
                
            }
            // this.setState({submitted:1});
            // console.log(response.data)
        });
      

    }

    handlerInsert=(event)=>{
        const tb=this;
       var data=[{ fid:event.target.value, sid:sid,lineage:line}] ;
    //    var data=event.target.value;
       localStorage.setItem('child',JSON.stringify(data));
       // console.log(localStorage.getItem('childid'));
       tb.props.history.push({pathname:'/add'});
        console.log('hello');
    }
    resultdata(){
        return  this.state.name.length > 0 ? this.state.name.map((data)=> {
            return (
                
                    <tr>
                       
                        <td>{data.pname}</td>
                        <td>{data.childname}</td>
                        <td>{data.lname}</td>
                        <td><button className="btn" onClick={this.handlerInsert}  value={data.childid}>जोड़ना</button></td>
                    </tr>
                )
            }): null
    }
   
    render() { 
        
        return (
            <div>
                <div className="header-banner">
                    <div className="container">
                        <h3>खोजें </h3>
                    </div>
		        </div>
				<div className="content">
				    <div className="contact">
                        <div className="container">
                            <div className="contact-grids">
                                <div className="col-md-12" >
                                
                                {this.state.err}
                                </div>
                                <div className="col-md-10 col-md-offset-1 contact-right">                               
                                    <form onSubmit={this.handlerSubmit}>
                                        <div className="col-md-5 frm">
                                            <input type="text" placeholder="पिता का नाम"  id="fname" name="fname" value={this.state.fname} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="text" placeholder="दादा का नाम"  id="gfname" name="gfname" value={this.state.gfname} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <select name="surname" id="surname" className="form-control" onChange={this.getlineage}>
                                            <option>उपनाम</option>
                                            { 
                                                this.state.snames.length > 0 ? this.state.snames.map(function(data) {
                                                return (
                                                <option value={data.id} key={data.id}>{data.sname}</option>
                                                );
                                            }): null
                                           }  
                                            </select>
                                            <h5></h5>
                                            <select name="lineage" id="lineage" className="form-control" onChange={this.setlineage}>
                                            <option>Select option</option>
                                            { 
                                                this.state.lnames.length > 0 ? this.state.lnames.map(function(data) {
                                                return (
                                                <option value={data.id} key={data.id}>{data.lname}</option>
                                                );
                                            }): <option disabled="true">no lineage</option>
                                           }  
                                            </select>
                                            {/* <input type="text" placeholder="sub-lineage" name="sublineage"/>
                                            <h5></h5> */}
                                            <input type="submit" value="खोजें"/>
                                        </div>
                                    </form>
                                    
                                    <div className="col-md-7">
                                    
                                    <table className="table" cellPadding="10px" cellSpacing="10px" >
                                        <tbody>
                                        <tr  cellPadding="10">
                                         
                                            <td>दादा का नाम</td>
                                            <td>पिता का नाम</td>
                                            <td>जाति</td>
                                            <td>परिवर्तन</td>
                                        </tr>
                                        {this.resultdata()}
                                       </tbody>
                                    </table>
                                    </div>
                                   
                                </div>
                               
                                <div className="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default withRouter(Searchtree);




