getData=(id)=>{
let data={id:id}
	 axios.post('/main/chart',data) // fetch from Express.js server
    .then(response =>{
    
    	this.handleChart(response.data)
    	
    	//this.setState({result:response.data})

	
    }).catch(error=>{
    	console.log(error);
    })

   
}
handleChart=(array)=>
{
       var map = {}
       for(var i = 0; i < array.length; i++){
           var obj = array[i]
           if(!(obj.id in map)){
               map[obj.id] = obj
               map[obj.id].children = []
           }
   
           if(typeof map[obj.id].fname === 'undefined'){
               map[obj.id].id = obj.id
               map[obj.id].name = obj.name
               map[obj.id].attr = obj.attr
               map[obj.id].parent= obj.parent
           }
   
           var parent = obj.parent || '-';
           if(!(parent in map)){
               map[parent] = {}
               map[parent].children = []
           }
   
           map[parent].children.push(map[obj.id])
       }
       let result= map['-']
	   console.log(result);
	
      
    
   }


