import React, { Component } from "react";
import { BrowserRouter, Route, history,Link, Redirect,  withRouter } from 'react-router-dom'
import Axios from "axios";
import $ from "jquery";
import api from "../api";
const host=api();
var sids='';
var line='';
class Nameadd extends Component {
    state = {
        redirect: false
      }
      
   constructor(props)
   {
       super(props);
       this.state={
           sid:'',
           name:'',
           fname:'',
           gfname:'',
           surname:'',
           lineage:'',
           err:'',
           snames:'',
          lnames:'',
          phone:'',
          mobile:'',
          wtsapno:'',
          address:'',
          child:'',
          email:'',
           msg:'',
           msg_status:0,
           progress:0,
          selectedFile:'',
          btn_disable:false
       }
       this.handlerSubmit=this.handlerSubmit.bind(this);
       this.getlineage=this.getlineage.bind(this)
         this.handlerChange=this.handlerChange.bind(this)
         this.handleFormClear=this.handleFormClear.bind(this)


   }
   setlineage=event=>{
    this.setState({
        lineage:event.target.value
    });
    console.log(this.state.lineage);
    }

    componentDidMount(){

        const that=this;
        Axios.get(host+"/main/sname")
        .then(function(response){
            const surnames=response.data;
            console.log(surnames);
            that.setState({snames:surnames});
        })
    }
    getlineage=event=>{
        const that=this;
       var index = event.target.selectedIndex;
       var optionElement = event.target.childNodes[index]
       var option =  optionElement.getAttribute('value');
        var target =  optionElement.getAttribute('data-target');

        console.log(target)
    

        Axios.post(host+"/main/lineagename",{sid:option})
        .then(function(response){
            const lineagenames=response.data;
            console.log(lineagenames);
            that.setState({lnames:lineagenames});
        });
         that.setState({
        sid:target
    });
      

    }
    handlerChange=event=>{
      switch(event.target.name)
      {
        case 'selectedFile':
        this.setState({selectedFile:event.target.files[0]});
        break;
        default:
        this.setState({[event.target.name]:event.target.value});
      }
       

    }
  
    handlerSubmit=(event)=>{
    
        event.preventDefault();
        const formData=new FormData();
        const that=this;
       const data={surname:that.state.sid,
            lineage:that.state.lineage,
            name:that.state.name,
            fname:that.state.fname,
            gfname:that.state.gfname,
            dob:that.state.dob,
            address:that.state.address,
            phone:that.state.phone,
            mobile:that.state.mobile,
            wtsapno:that.state.wtsapno,
            email:that.state.email
          }
  const config = {
          onUploadProgress: function(progressEvent) {
            var percentCompleted = Math.round( (progressEvent.loaded * 100) / progressEvent.total )
           that.setState({progress:
              Math.round( (progressEvent.loaded * 100) / progressEvent.total )})
            console.log(percentCompleted)
          }
        }
            formData.append('data',JSON.stringify(data));
            formData.append('selectedFile',that.state.selectedFile);
          
 
        Axios.post(host+"/main/addreq",formData, config)
        .then(function(response) {
          that.setState({btn_disable:true})
         that.setState({msg_status:1})

            if (response.data.msg === 'success'){
               
              that.handleFormClear(this)

                alert("आपका नाम जोड़ने का अनुरोध व्यवस्थापक को भेज दिया गया है"); 
              
                 
            }else if(response.data.msg === 'fail'){
              that.handleFormClear(this)
                alert("आपका नाम जोड़ने का अनुरोध व्यवस्थापक को नहीं भेजा गया है")
            }else
            {
              that.handleFormClear(this)
               alert("पहचान पत्र अपलोड  कीजिये");
            }
        });
    }
handleFormClear=()=>{
this.setState({
           sid:'',
           name:'',
           fname:'',
           gfname:'',
           surname:'',
           lineage:'',
           err:'',
           snames:'',
          lnames:'',
          phone:'',
          mobile:'',
          wtsapno:'',
          address:'',
          child:'',
          email:'',
          msg:'',
          msg_status:0,
           progress:0,
         selectedFile:'',
          btn_disable:false
      })
}

   
    render() { 
     
        return (
            <div>
         
                <div className="header-banner add">
                    <div className="container">
                        <h3>नाम जोड़ें</h3>
                    </div>
      		        </div>
      				     <div className="content">
      				    <div className="contact">
                        <div className="container">
                            <div className="contact-grids">
                            <div className="col-md-12 notice" >
                                ध्यान दें : कृपया हिन्दी की-बोर्ड डाउनलोड करने के लिए <a href="https://download.cnet.com/Axar-Hindi-Keyboard/3000-20432_4-75737380.html" target="_blank"> यहां क्लिक करें  </a> ।
                                     {this.state.err}
                                 
                                </div>
                                <div className="col-md-10 col-md-offset-1 contact-right frm">                               
                                    <form onSubmit={this.handlerSubmit.bind(this)} method="post" encType="multipart/form-data">
                                    <div className="col-md-6">
                                            <input type="text" placeholder="नाम" name="name"  id="name" required  value={this.state.name} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="text" placeholder="पिता का नाम" name="fname" id="fname" required  value={this.state.fname} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="text" placeholder="दादा का नाम" name="gfname" id="gfname" required  value={this.state.gfname} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            {/* <input type="text" placeholder="खांप (जाति)" name="surname" id="surname" /> */}
                                            <select name="surname" id="surname" className="form-control" onChange={this.getlineage}>
                                            <option>खांप (जाति)</option>
                                            { 
                                                this.state.snames.length > 0 ? this.state.snames.map(function(data) {
                                                return (
                                                <option value={data.id} data-target={data.sname} key={data.id}>{data.sname}</option>
                                                );
                                            }): null
                                           }  
                                            </select>
                                            <h5></h5>
                                            {/* <input type="text" placeholder="उप खांप" name="lineage" id="lineage" /> */}
                                            <select name="lineage" className="form-control" id="lineage" onChange={this.setlineage}>
                                            <option>उप खांप</option>
                                            { 
                                                this.state.lnames.length > 0 ? this.state.lnames.map(function(data) {
                                                return (
                                                <option value={data.lname} key={data.id}>{data.lname}</option>
                                                );
                                            }): <option disabled={true}>no lineage</option>
                                           }  
                                            </select>
                                            <h5></h5>
                                            <input type="date" placeholder="जन्म तारीख" name="dob" id="dob" className="form-control" value={this.state.dob} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="email" placeholder="इ-मेल" name="email" id="email" className="form-control"  value={this.state.email} onChange={this.handlerChange}/>
                                            <h5></h5>
                                        </div>
                                        <div className="col-md-6">
                                            <input type="text" placeholder="स्थाई पता" name="address" id="address"  value={this.state.address} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="text" placeholder="फोन नम्बर" name="phone" id="phone" value={this.state.phone} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="text" placeholder="मोबाईल नम्बर" name="mobile" id="mobile"  value={this.state.mobile} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            <input type="text" placeholder="वाट्सएप नम्बर" name="wtsapno" id="wtsapno"  value={this.state.wtsapno} onChange={this.handlerChange}/>
                                           
                                            <h5>पहचान पत्र</h5>
                                            <input type="file" name="selectedFile"   onChange={this.handlerChange} accept="image/*"/><br/>
                                         { this.state.progress!=100 && this.state.msg_status!=1?null:([
                                               <div class="progress">
                                               <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow={{width:this.state.progress}}
                                            aria-valuemin="0" aria-valuemax="100" style={{width:this.state.progress+"%",backgroundColor:"#DA6A3C"}}>
                                              {this.state.progress}
                                              
                                            </div>
                                          </div>]) }





                                        </div>
                                        <div className="col-md-12">
                                            <input type="submit" value="जमा करें" disabled={this.state.btn_disable}/>
                                        </div>
                                    </form>
                                </div>
                               
                                <div className="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}
export default withRouter(Nameadd);




