import React, { Component } from "react";
import  axios from 'axios';
import api from "../api";
const host=api();
class Register extends Component {
    constructor(props) {
        super(props);
        this.state={
            name:'',
            email:'',
            password:''
      };
    }

  handleInputChange(event) {
    const target = event.target;
    const value =  target.value;
    const name = target.name;
 
    this.setState({
      [name]: value
    });
  }
 
    signDataHandler=event=>{
        event.preventDefault();
        console.log('who');
 
        const data={
            name:this.state.name,
            email:this.state.email,
            password:this.state.password,
        };
        axios.post(host+"/register",data)
         .then(response=>{
            console.log(response);

        }).catch(err=>{
            console.log(err);});
    };
    render(){
        return(
            <React.Fragment>
                <div className="col-md-6">
                <form role="form"className="form-horizontal" onSubmit={this.signDataHandler}>
 
                <div className="form-group ">
                   <label className="control-label col-sm-2">Name:</label>
                   <div className="col-sm-5">
                   <input type="text" className="form-control" id="name" name="name" placeholder="Enter name" onChange={this.handleInputChange.bind(this)} value={this.state.name}/>
                   </div>
                   </div>
 
                   <div className="form-group ">
                   <label className="control-label col-sm-2" for="email">Email:</label>
                   <div className="col-sm-5">
                   <input type="email" className="form-control" id="email" name="email" placeholder="Enter email"  onChange={this.handleInputChange.bind(this)} value={this.state.email} />
                   </div>
                   </div>
 
                <div className="form-group">
                   <label className="control-label col-sm-2" for="pwd">Password:</label>
                   <div className="col-sm-5">
                   <input type="password" className="form-control" id="pwd" name="password" placeholder="Enter password"  onChange={this.handleInputChange.bind(this)} value={this.state.password} />
                  </div>
                </div>
 
                 <div className="form-group">
                   <label className="control-label col-sm-2" for="con_pwd">Confirm Password:</label>
                   <div className="col-sm-5">
                   <input type="password" className="form-control" id="con_pwd" name="con_pwd" placeholder="Enter Confirm password"/>
                  </div>
                </div>
 
 
                 <div class="form-group ">
                   <div class="col-sm-offset-2 col-sm-10">
                   <button type="submit" class="btn btn-default " id="sign-btn" onClick={this.signDataHandler}>Sign Up</button>
                   </div>
                </div>
                   </form>
                </div>
            </React.Fragment>
        );
 
    }
}
export default Register;




