import React, { Component } from "react";
import { BrowserRouter, Route, history, Redirect,  withRouter } from 'react-router-dom'
import Axios from "axios";
import './cult.css';
import test from "./textconvert";
import api from "../api";
const host=api()
var sid='';
var line='';
class Add extends Component {
    state = {
        redirect: false
      }
      
   constructor(props)
   {
       super(props);
       this.state={
           name:'',
           surname:'',
           lineage:'',
           err:'',
           snames:'',
          lnames:'',
       }
       this.handlerSubmit=this.handlerSubmit.bind(this);
       this.handlerChange=this.handlerChange.bind(this);
   }
    handlerChange=event=>{
        this.setState({
            [event.target.id]:event.target.value
        })

    }
  
    handlerSubmit=(event)=>{
        const tb=this;
       var unicode_name=test(this.state.name);
       console.log(unicode_name)
       event.preventDefault();
        Axios.post(host+"/main/search",{
            name:unicode_name,
            surname:sid,
            lineage:this.state.lineage
        })
        .then(function(response) {
           console.log(response.data)
            if(response.status=='200' && response.data.er==1){
               tb.setState({err:'नाम नही मिला है'});
            }
            else{
                var data=tb.state.lineage
                 tb.setState({err:'नाम मिला है'});
                // localStorage.setItem('data',JSON.stringify(data));
                 tb.props.history.push({pathname:`/chart/`+data});
            }
        });
    }
    componentDidMount() {
        const that=this;
        Axios.get(host+"main/sname")
        .then(function(response){
            const surnames=response.data;
            console.log(surnames);
            that.setState({snames:surnames});
        })
    }
    getlineage=event=>{
        const that=this;
        sid=event.target.value;

        Axios.post(host+"main/lineagename",{sid:sid})
        .then(function(response){
            const lineagenames=response.data;
            console.log(lineagenames);
            that.setState({lnames:lineagenames});
        })
    }
    setlineage=event=>{
        this.setState({
            lineage:event.target.value
        });
        console.log(this.state.lineage);
        }
    render() {
        return (
            <div style={{marginBottom:"4em"}}>
                <div className="header-banner ">
                    <div className="container">
                        <h3>खोजें </h3>
                    </div>
		        </div>
				<div className="content">
				    <div className="contact">
                        <div className="container">
                            <div className="contact-grids">
                                <div className="col-md-12 notice" >
                                ध्यान दें : कृपया हिन्दी की-बोर्ड डाउनलोड करने के लिए <a href="https://download.cnet.com/Axar-Hindi-Keyboard/3000-20432_4-75737380.html" target="_blank"> यहां क्लिक करें  </a> ।
                                     
                                </div>
                                <div className="col-md-12 notice">
                                 {this.state.err}

                                </div>
                                <div className="col-md-10 col-md-offset-1 contact-right">
                                        {/* <div className="col-md-4">
                                            <input type="text" placeholder="name" name="name"/>
                                            <h5></h5>
                                            <input type="text" placeholder="surname" name="surname"/>
                                            <h5></h5>
                                            <input type="date" name="dob"/>
                                            <h5></h5>
                                            <input type="submit" value="send"/>
                                        </div>*/}
                                    {/* <form onSubmit={this.handlerSubmitfg}>
                                        <div classNameName="col-md-4">
                                            <input type="text" placeholder="नाम"/>
                                            <h5></h5>
                                            <input type="text" placeholder="उपनाम"/>
                                            <h5></h5>
                                            <input type="text" placeholder="पिता का नाम"/>
                                            <h5></h5>
                                            <input type="text" placeholder="दादा का नाम"/>
                                            <h5></h5>
                                            <input type="submit" value="खोजें"/>
                                        </div> 
                                    </form> */}
                                    <form onSubmit={this.handlerSubmit}>
                                        <div className="col-md-6 col-md-offset-3 frmpad">
                                            <input type="text" placeholder="नाम" id="name" name="name" value={this.state.name} onChange={this.handlerChange}/>
                                            <h5></h5>
                                            {/* <input type="text" placeholder="उपनाम" id="surname" name="surname" value={this.state.surname} onChange={this.handlerChange}/> */}
                                            <select name="surname" id="surname" className="ances form-control" onChange={this.getlineage}>
                                            <option>उपनाम</option>
                                            { 
                                                this.state.snames.length > 0 ? this.state.snames.map(function(data) {
                                                return (
                                                <option value={data.id} key={data.id}>{data.sname}</option>
                                                );
                                            }): null
                                           }  
                                            </select>
                                            <h5></h5>
                                            {/* <input type="text" placeholder="उप - उपनाम" id="lineage" name="lineage" value={this.state.lineage} onChange={this.handlerChange}/> */}
                                            <select name="lineage" id="lineage" className="ances form-control" onChange={this.setlineage}>
                                            <option>उप खांप</option>
                                            { 
                                                this.state.lnames.length > 0 ? this.state.lnames.map(function(data) {
                                                return (
                                                <option value={data.id} key={data.id}>{data.lname}</option>
                                                );
                                            }): <option disabled="true">no lineage</option>
                                           }  
                                            </select>
                                            <h5></h5>
                                            {/* <input type="text" placeholder="sub-lineage" name="sublineage"/>
                                            <h5></h5> */}
                                            <input type="submit" value="भेजें"/>
                                        </div>
                                    </form>
                                   
                                </div>
                               
                                <div className="clearfix"></div>
                            </div>
						</div>
					</div>
		        </div>
            </div>
        );
    }
}

export default withRouter(Add);




