import React, { Component } from "react";
import  axios from 'axios';
import './cult.css';
import ReactDOM from 'react-dom';
import ReactImageZoom from 'react-image-zoom';
import api from "../api";
const host=api();

const props = {width: 400, height: 250, zoomWidth: 500, img: 'images/gotrachart.jpg'};
class History extends Component {
    constructor(props) {
        super(props);
        this.state={
            name:'',
            image:''
           
			
		}
	 
    }
    
	componentDidMount() {
		axios.get(host+'/main/history') // fetch from Express.js server
		.then(response =>{
			const names=response.data;
			console.log(response.data);
           
            const props = {width: 2000, height: 150, zoomWidth: 50, img: response.data[0].image};
		})
	}
    render() {
        

        return (
            <div>
                <div className="header-banner hist">
                    <div className="container">
                        <h3>इतिहास</h3>
                    </div>
                </div>
                <div className="about-section">
                    <div className="container">
                        <div className="about-grids">
                            <div className="col-md-10 col-md-offset-1 about-grid">
                                
                                
                                <ReactImageZoom {...props} className="img-responsive" />
                            </div>
                           
                            <div className="col-md-12 about-grid aboutdes" style={{marginTop:"2em"}}>
                    <h4 className="text-center">पुष्करणा जाति का उद्भव ऐतिहासिक द्दष्टि </h4> 
                                <p >राजस्थान की सांस्कृतिक राजधानी राव जोधा द्वारा बसायी गई। शक्ति और भक्ति की संगम स्थली, रणबंका राठौड़ों की कर्म भूमि और वैज्ञानिकों द्वारा सम्मानित सूर्यनगरी जोधपुर विविध जातियों की प्रतिभा से आलोकित है। इन विभिन्न जातियों में राजनैतिक रूप से चेतन, सामाजिक रूप से जागृत, धार्मिक भाव से श्रद्धालु, आर्थिक रूप से अधिकांशंतः उच्च मध्यवर्गीय तथा सांस्कृतिक द्दष्टि से संस्कार प्रिय तथा कला के क्षेत्र में अपूर्व प्रतिभाशाली पुष्करणा समाज की अपनी मौलिक व विशिष्ट आचरण शैली के कारण अलग छवियुक्त है। इतिहास साक्षी है कि जोधपुर में पुष्करणा समाज का निवास उतना ही पुरातन है जितना कि मयूरध्वज दुर्ग जिसे आज मेहरानगढ़ के रूप में विश्व ख्याति प्राप्त है। जोधपुर के संस्थापक राठौड़ वंश  चुड़ामणि राव जोधा के अनन्य सहयोगी के रूप में पुष्करणा जाति के यहां निवास रहा है। 
    <br/>ई. वर्ष 1459 से अद्यतन पुष्करणा समाज के मध्यकालीन यौद्धा राजदरबारी, राजसेवक, कला साधक, व्यापारी, शिक्षक ही नहीं बल्कि विगत शताब्दी में अनेक राजनीतिज्ञ भी जोधपुर के विकास में सहायक रहे हैं। जोधपुर और पुष्करणा समाज का सम्बन्ध धूप-छांव जैसा अभिन्न रहा है। ज्ञान, विज्ञान, कला और व्यवसाय के क्षेत्र में पुष्करणा प्रतिभाएं जोधपुर ही नहीं बल्कि विदेशों तथा देश की अनेक भूमि पर सम्मानित होती रही है।
    <br/>
    जोधपुर के पुष्करणा समाज का यह वैशिष्ठ्य है कि यद्यपि वह परिश्रमी, प्रतिभाशाली, पुरूषार्थी एवं बहुमुखी कला कौशल का धनी है, वह वाक्सिद्ध है। तथापि विडम्बना यह है कि वह अपने इतिहास के प्रति निरन्तर उदासीन भी रहा है। अपने गौरवशाली इतिहास के प्रति उसकी उदासीनता वर्तमान में जीने वाली युवा पीढ़ी के लिए एक बड़ा यक्ष प्रश्न रहा है।
    <br/>
    वर्तमान पीढ़ी की बौद्धिकता केवल वैयक्तिक सुख-समृद्धि के संसाधनों के जोड़-तोड़ तक सिमट कर रह गई है। उनका सम्पूर्ण श्रम-कर्म व्यापार भौतिक समृद्धि पर केन्द्रित हो गया है। इसका एक पक्ष तो ठीक ही कहा जायेगा, इसमें कोई संदेह नहीं है। चिन्ता का दूसरा पहलु यह है कि हम गेहॅू में तो उलझ कर रह गये लेकिन गुलाब को भूला रहे है, मेरा संकेत है- पुष्करणा समाज की प्राचीन और गौरवशाली परम्परा जिसका अभिज्ञान कराने का सबसे बड़ा स्त्रोत है-वंशावली ज्ञान। वंशावली केवल पुरखों का नाम स्मरण ही नहीं होती है, उसके साथ यह हमें अपने अतीत से जोड़ने का सामाजिक माध्यम होती है। यह हमारे अतीत के गौरवशाली चरित्रात्माओं का सशब्द ज्ञान का स्त्रोत कही जा सकती है। यह सामाजिक एकता को दर्शाने वाला दर्पण है। 
    <br/>
    यह एक स्थापित सत्य है कि अतीत शून्य वर्तमान का कोई महत्व नहीं होेता। कोरे वर्तमान के उज्जवल भविष्य की कल्पना करना हथेली पर सरसों जमाने जैसा ही कार्य होता है। इसलिए वंशावली का अभिज्ञान हमें समाज के उद्गम स्त्रोत तक पहुंचाता है। मैं समझता हूूॅ हमारे पुष्करणा समाज की यह नितान्त अपरिहार्य सांस्कृतिक आवश्यकता है। पुष्करणा समाज की उत्पति का प्रामाणिक वृतांत ले देकर दो ग्रंथों में उल्लेखित रह गया है। कर्नल जेम्स टाॅड ने सर्वप्रथम अपने ‘एनल्स एण्ड एण्टीक्विटिज आॅफ राजपुताना’ में एक जनप्रवाद के प्रचलन को प्रस्तुत करके लिख दिया कि ‘पुष्करणा जाति तो ओड है।’ कर्नल टाॅड की यह मान्यता कालक्रम के अनुसार रूढ होती गई।
    <br/>
कालान्तर में एक तत्व शोधक, परम मेधावी, पुष्करणा समाज कुलरत्न एवं इतिहास द्दष्टि सम्पन्न श्री मीठालालजी व्यास ने सतर्क प्रभाव सहित उदाहरणों के साथ अपनी प्रसिद्ध कृति के माध्यम से तत्सम्बन्धी कर्नल टाॅड की भयानक भूल को सिद्ध कर दिया। उन्होंने टाॅड लिखित तथाकथित लोक मान्यताओं पर आधारित अपने ‘इतिहास’ ग्रंथ के दूसरे भाग में जैसलमेर के इतिहास के अन्तर्गत इस ओड प्रसंग का खण्डन किया। उन्होंने सप्रमाण कि संभवतः स्वयं टाॅड को भी इस जन प्रचलित प्रवाद पर पूर्ण विश्वास नहीं रहा होगा। तभी तो इस प्रसंग को उन्होंने ‘लोक अफवाह’ तथा ‘अजब कहानी’ कहा। अजमेर की तवारीख में कहीं भी पुष्करणा ब्राह्मणों का वृतान्त उल्लेखित नहीं है। 
<br/>
‘रिपोर्ट मर्दुम शुमारी, राज्य मारवाड़’ में स्पष्ट उल्लेख है कि पुष्कर में रेत की खुदाई करने वाले नाहरराव पड़िहार से कई शताब्दी पूर्व पुष्करणा जाति के होने का प्रमाण मिलता है। एतद्र्थ बीस हजार ओडों को जनेऊ धारण करवाकर उन्हें ‘पुष्करणा’ बनाने की धारणा अस्वीकार्य है। अत्यन्त पुरातन काल में महर्षि मौनक कृत ‘चरण व्यूह’ ग्रंथ में चारों वेदों की शाखाओं का उल्लेख है। उनमें एक भेद ‘पुष्करणीया’ है, संभवतः इसी से आगे चलकर ‘पुष्करणा’ शब्द की उत्पति हुई होगी। अंग्रेज इतिहास लेखक पादरी एम.ए. शैरिंग साहब ने अपने इतिहास ग्रंथ में पुष्करणा ब्राह्मणों की गणना पंच द्राविडों के अंतर्गत गुर्जर ब्राह्मण में की है। श्री मीठालाल व्यासजी ने स्कन्द पुराण में वर्णित श्रीमाल क्षेत्र माहात्म्य के अंतर्गत लक्ष्मीजी द्वारा ‘पुष्टि’ किये जाने के कारण श्रीमाल क्षेत्र के ब्राह्मण ‘पुष्टिकर’ कहलाये जो कालानतर में अपभ्रष्ट होकर ‘पुष्करणा’ अथवा ‘पोकरणा’ हो गये। इतना अवश्य सत्य है कि पुष्करणा ब्राह्मण पंच द्राविडों के अंतर्गत गुर्जर ब्राह्मणों का एक भेद है। पुष्करणा ब्राह्मणों के अस्तित्व की पुरातन परंपरा को श्री मीठालाल व्यासजी ने अपने ग्रंथ ‘टाॅड की भूल’ में विस्तार से प्रस्तुत किया है। यहां विस्तार भय से उसका अधिक उल्लेख नहीं किया गया है।
<br/>
आज पश्चिमी सभ्यता तथा अपसंस्कृति की चकाचैंध में भटकती युवा पीढ़ी को उनकी गौरवशाली पुरातन परंपरा का स्मरण दिलाना आवश्यक है। यह एक कुनैनी सत्य है कि विभिन्न समाजों के तथाकथित शिक्षित एवं बौद्धिक बात भले ही विज्ञान की करते हों लेकिन उनकी गहराई से पड़ताल की जाये तो वे भी अपनी जातिगत भावना से कहीं भी पृथक नहीं है तब पुष्करणा समाज अपनी सनातन परंपरा के समृद्ध ज्ञान से वंचित क्यों रहे? हमारी एक समस्या यह भी है कि विगत वर्षों में हमारी जाति का वैज्ञानिक इतिहास भी किसी ने न जानने और न ही लिखने का प्रयास किया। उनकी द्दष्टि में यह अनुपयोगी व कालातीत व्यर्थ श्रम है। वे यह जानने की चेष्टा नहीं करते कि कोई भी समाज भली भांति उन्नति तब तक नहीं कर सकता जब तक कि उसका अपना तर्क सम्मत इतिहास न हो। वंशावली बोध भी इसी महत्कार्य का एक अंग है। हम अधिकतम पितामह और प्रपितामह तक ही जानकारी दे सकते है। जन्म से मृत्यु पर्यन्त अनेक ऐसे आयोजन होते है जहां पितामह, प्रपितामह, मातामही, प्रमातामही का नाम और गौत्र का उच्चारण करना अनिवार्य होता है। वहां हम पढ़े लिखे प्रबुद्धों की हास्यास्पद स्थिति हो जाती है।
<br/>
एत्दर्थ, आज हमें संकल्पबद्ध होकर उस महान सारस्वत अनुष्ठान की सम्पूर्ति में सहायक होना चाहिए जहां हमारे समाज की विभिन्न शाखाओं की वंशावली निर्मित की जा रही है। यह समयनष्टी कार्य नहीं बल्कि ‘जाति गौरव बोध’ यज्ञ है। आईये! हम सभी इस ज्ञान यज्ञ की सार्थक परिणति में सहायक बनें।
<br/>
आज का युग वैज्ञानिक समझ का है। तार्किक बौद्धिकता है। जाति, वर्ण व्यवस्था अब गौण हो गये है फिर भी यह भी अकाट्य सत्य है कि किसी भी समाज की अपनी कुछ मौलिक विशेषता होती है और वही उसका वैशिष्ठ्य भी होता है। हमारे वर्तमान का निर्माण बीते हुए कल ने किया है और आगामी कल के निर्माण में आज का महत्व है। भूत ही वर्तमान को बनाता है और वर्तमान ही भविष्य का सृजन करता है। एतदर्थ, यदि हम हमारे वंशजों की परंपरा से विछिन्न होते है तो हमारी स्थिति जड़ बिन वृक्ष की हो जायेगी। आज भले ही वह हरा भरा दिखलाई देता हो लेकिन आगामी कल तो उसे अवश्य ही शुष्क बनाकर धराशायी कर देगा। वंशावली हमारी अस्मिता है, पध्यान है, वजूद है, हमारी ओळखाण है।
<br/>
वंशावली निर्माण का कार्य एक बड़ी तपश्चर्या है। कठोर श्रमसाध्य साधना है। हमारे समाज में इस दिशा में पूर्व भागीरथ प्रयास श्रद्धेय पुरूषोतमदास पुरोहितजी ने किया। इस कार्य को आगे बढ़ाने की दिशा में मैं भी प्रयासरत हॅू। किंचित सफलता भी मिली है फिर भी बहुत सा कार्य शेष है जिसके लिए आप सबका सहयोग अपेक्षित है। आईये! आप और हम सब संयुक्त होकर पुष्करणा समाज की इस विरासत को अग्रसर करेें।
</p>
                            </div>
                            <div className="clearfix"></div>
                        </div>
                    </div>
                </div>
        </div>
        );
    }
}
export default History;



