import React, { Component } from "react";
import  axios from 'axios';
import './cult.css';
import api from "../api";
const host =api();
class Home extends Component {
  
  constructor(props) {
    super(props);
    this.state={
        name:'',
        image:''  
}

}

componentDidMount() {
axios.get(host+"/main/welcome") // fetch from Express.js server
.then(response =>{
  const names=response.data;
  console.log(response);
  this.setState({name:response.data[0].desc,image:response.data[0].image});
})
}
    render() {
        return (
            <div>
              <div className="bottom-banner">
                <div className="container">
                
                </div>
              </div>
              <div className="content">
                <div className="welcome">
                  <div className="container">
                    <h3>पुष्करणा ब्राह्मणों का उद्भव</h3>
                    <div className="welcome-grids">
                      <div className="col-md-4 welcome-grid">
                        <div className="welcome1">
                          <figure className="effect-bubba">
                            {/* <img src="images/green.jpg" className="img-responsive" alt="/"/> */}
                            <img src={this.state.image} className="img-responsive" alt="/"/>
                            <figcaption>
                            
                            </figcaption>     
                          </figure> 
                        </div>
                      </div>
                      <div className="col-md-7 welcome-grid wel1 weldes">
                        <p>{this.state.name}</p>
                      </div>
                      <div className="clearfix"></div>
                    </div>
                  </div>
                </div>
                </div>
              </div>
        );
    }
}
export default Home;




