import React, { Component } from "react";
import  axios from 'axios';
import './cult.css';
import api from '../api.js';
class About extends Component {
    constructor(props) {
        super(props);
        this.state={
            name:'',
            image:'',
            desc:''
			
		}
	 
    }
    
	componentDidMount() {
       const host=api();

		axios.get(host+'/main/about') // fetch from Express.js server
		.then(response =>{
			const names=response.data;
			console.log(response.data);
			this.setState({name:response.data[0].desc,image:response.data[0].image,desc:response.data[0].desc2});
		})
	}
    render() {
        return (
            <div>
                <div className="header-banner">
                    <div className="container">
                        <h3>परिचय</h3>
                    </div>
                </div>
                <div className="about-section">
                    <div className="container">
                        <div className="about-grids">
                            <div className="col-md-4 col-md-offset-4 about-grid">
                                <img src={this.state.image} className="img-responsive aboutimg" alt="/"/>
                              
                            </div>
                           <div className="col-md-12 about-grid aboutdes">
                                <h4 className="text-center">सदाशिवन् व्यास एक संकल्प एक चुनौती</h4>
                                    <p>{this.state.name}</p>
                            </div> 
                            <div className="col-md-12  about-grid aboutdes">
                                <h4 className="text-center">जीवन परिचय</h4>
                                <p>{this.state.desc}</p>
                            </div>
                           
                            <div className="clearfix"></div>
                        </div>
                    </div>
                </div>
              
           
        </div>
        );
    }
}
export default About;



